import {createReducer} from 'redux-decorated'
import {actions} from '../actions'

export const invite = createReducer({})
  .when(actions.inviteUpdate, (state, newState) => {
    if (newState.user) {
      newState.user = {...state.user, ...newState.user}
    }

    return {
      ...state,
      ...newState,
    }
  })
